import path from 'path';

import fs from 'fs-extra';
import templateFile from '@expo/template-file';

import { Credentials } from '../credentials';

const TEMPLATE_FILE_PATH = path.join(__dirname, '../../../../templates/Gymfile.template');

interface GymfileOptions {
  nativeProjectDirectory: string;
  scheme: string;
  outputName: string;
  outputDirectory: string;
}

async function createIfNotExists(
  credentials: Credentials,
  { nativeProjectDirectory, scheme, outputName, outputDirectory }: GymfileOptions,
): Promise<boolean> {
  const outputFile = path.join(nativeProjectDirectory, 'Gymfile');

  if (await fs.pathExists(outputFile)) {
    return false;
  } else {
    await templateFile(
      TEMPLATE_FILE_PATH,
      {
        BUNDLE_IDENTIFIER: credentials.bundleIdentifier,
        PROVISIONING_PROFILE_UUID: credentials.provisioningProfileUUID,
        KEYCHAIN_PATH: credentials.keychainPath,
        SCHEME: scheme,
        OUTPUT_DIRECTORY: outputDirectory,
        OUTPUT_NAME: outputName,
      },
      outputFile,
    );
    return true;
  }
}

export { createIfNotExists };
